.container {
  width: 100%;
  max-width: 75rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.bg-primary {
  background-color: #E2A22F !important;
}

.text-primary {
  color: #E2A22F !important;
}

.text-secondary {
  color: white !important;
}

.bg-primary {
  background-color: #E2A22F !important;
}

.border-primary {
  border-color: #E2A22F !important;
}

.btn-primary {
  border-color: #E2A22F !important;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
li {
  padding-left: 0;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}
@media (max-width: 450px) {
  html {
    font-size: 80%;
  }
}

body {
  line-height: 1.15 !important;
  font-family: "Roboto Mono", monospace;
}
body.active {
  overflow-y: hidden;
}

a,
ul {
  text-decoration: none;
  list-style: none;
  color: inherit !important;
}

.border-start {
  border-left: 0.4rem solid #E2A22F !important;
}

.portfolio-grid {
  display: grid;
  grid-gap: 0.6rem;
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
}
@media (max-width: 400px) {
  .portfolio-grid {
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  }
}

.img-fluid {
  aspect-ratio: 4/4.3 !important;
}

.hoverPrimary:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
  background: none !important;
  color: #E2A22F !important;
}

.hoverSecondary:hover {
  background: white !important;
  color: #E2A22F !important;
  transition: all 0.3s ease-in-out;
}

@media (max-width: 768px) {
  .d-sm-none {
    display: none;
  }
}

.fs-5,
.fs-6 {
  font-size: clamp(1rem + 0.5vh);
}

.btn:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}

.hero {
  z-index: 10;
}
.hero .rounded-pill {
  z-index: 100;
  position: relative;
}
.hero .fa, .hero .fas, .hero .fad {
  font-size: 5rem;
}

.header {
  width: 100%;
  height: 4.75rem;
  position: fixed;
  top: 0;
  left: 0;
  margin: auto;
  background: #000;
  z-index: 1000;
}
.header .menuToggler {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 0.5rem;
  right: 1rem;
  cursor: pointer;
  display: none;
}
@media only screen and (max-width: 768px) {
  .header .menuToggler {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
  }
  .header .nav-list {
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 4.75rem;
    right: -100%;
    background: #3d023d;
    clip-path: circle(0% at 93.5% 8%);
    flex-direction: column !important;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    transition: all 0.3s ease-in-out;
  }
  .header .nav-list .nav-link {
    font-size: 1.5rem;
  }
  .header .nav-list.active {
    clip-path: circle(110% at 93.5% 8%);
    right: 0;
    opacity: 0.8;
  }
}

.intro {
  background: #04012d;
  margin-top: -3.5rem;
}
.intro .intro-img .img-con {
  position: relative;
  padding-left: 1rem;
  padding-right: 1rem;
}
.intro .intro-img .img-con::after {
  content: "";
  position: absolute;
  top: -0.5rem;
  left: 0.5rem;
  width: 7rem;
  height: 7rem;
  border-top: 2px solid #E2A22F;
  border-left: 2px solid #E2A22F;
}
.intro .intro-img .img-con::before {
  content: "";
  position: absolute;
  bottom: -0.5rem;
  right: 0.5rem;
  width: 7rem;
  height: 7rem;
  border-bottom: 2px solid #E2A22F;
  border-right: 2px solid #E2A22F;
}

.skill .after {
  position: relative;
}
.skill .after::after {
  position: absolute;
  top: 0;
  right: 0;
  content: attr(name) "";
}
.skill .progress {
  background: none !important;
}
.skill .progress .progress-bar {
  background: #E2A22F;
}

.qualification {
  background: #1a0011;
}
.qualification .list-group-item {
  padding-left: 1.3rem;
  position: relative;
  margin-bottom: 1rem;
}
.qualification .list-group-item::after {
  content: "";
  position: absolute;
  top: 0;
  left: -0.71rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: #E2A22F;
}

.services {
  background: #0a001a;
  perspective: 1mm;
}
.services .card {
  border-radius: 0;
  border-top-right-radius: 1.8rem;
  box-shadow: 0 0 0.3rem rgba(255, 255, 255, 0.125);
}
.services .card:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}
.services .card:hover i {
  background: white !important;
  color: #E2A22F;
}
.services .card .card-header {
  margin-top: -2.5rem;
  border: none;
}
.services .card i {
  border-radius: 0.5rem;
}

.portfolio {
  background: #070119;
}
.portfolio .portfolio-grid {
  display: grid;
  grid-gap: 0.6rem;
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)) !important;
}
@media (max-width: 400px) {
  .portfolio .portfolio-grid {
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  }
}

.testimonial {
  background: #0a0132;
}
.testimonial .img-fluid {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}
.testimonial .text-sm-center {
  text-align: left !important;
}
@media (max-width: 768px) {
  .testimonial .text-sm-center {
    text-align: center !important;
  }
}

.blog {
  background: #06011e;
}

.contact .form-control {
  border-radius: 0 !important;
  font-size: 0.875rem;
  font-weight: 600;
  background: transparent;
  color: #fff;
  border: 0.05rem solid white;
}
.contact .btn-lg:hover {
  background: white !important;
  color: #E2A22F !important;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 500px) {
  .contact .address-line {
    text-align: center;
  }
  .contact .address-line .d-flex {
    text-align: left;
    align-items: flex-start;
    justify-content: center;
  }
}
.contact .address-line .me-2 {
  width: 2.5rem;
  height: 2.5rem;
  display: grid !important;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: #E2A22F;
}
.contact .address-line .fa {
  font-size: 1.5rem;
}

.blog-hero {
  background: url(/src/img/blog-banner.jpg) no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 50vh;
  background-color: rgba(0, 0, 0, 0.1);
  background-blend-mode: overlay;
}

.blog-showcase .hoverSecondary:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
  background: none !important;
  color: white !important;
}

.resume .cv-social i {
  width: 3rem;
  height: 3rem;
  background: #E2A22F;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  border-radius: 50%;
}
.resume .hobby {
  background: #0c0128;
}
.resume .hobby p {
  width: 8rem;
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid white !important;
  border-radius: 50%;
}

.footer .social .d-flex {
  width: 2rem;
  height: 2rem;
  align-items: center;
  justify-content: center;
}
.footer .social .d-flex .fa {
  font-size: 1.3rem;
}