.qualification{
    background: hsl(320, 100%, 5%);

    .list-group-item{
        padding-left: 1.3rem;
        position: relative;
        margin-bottom: 1rem;

        &::after{
            content: "";
            position: absolute;
            top: 0;
            left: -.71rem;
            width: 1rem;
            height: 1rem;
            border-radius: 50%;
            background: $primary;
        }
    }
}